<template>
 <div class="list" v-if="isReady">
       <div class="item" v-for="(item,index) in favoriteList" :key="index">
                <iframe id="player" class="video" :src="embedUrl +item.snippet.resourceId.videoId+`?enablejsapi=1&allowsInlineMediaPlayback=true&playsinline=1`" width="300" height="300"></iframe>
                    <div class="detail">
                        <img :src="icon" class="icon">
                        <div class=right>
                            <div class="title">{{item.snippet.title}}</div> 
                            <div class="owner">{{item.snippet.channelTitle}}</div>
                            <p class="description"> {{item.snippet.description}}</p>
                            <button @click="deleteFavorite(item.id)">delete</button>
                        </div>
                    </div>
        </div>         
</div>
</template>
<script>
import axios from 'axios'
export default {
    name:'Favorite',
    data(){
        return{
            isReady:true,
            getUserUrl:'https://www.googleapis.com/youtube/v3/channels?part=contentDetails&mine=true&key=AIzaSyAWVhkyOnTdrJOJZizvsUuB8UvIgUH77Ew',
            allListsUrl:'https://www.googleapis.com/youtube/v3/channels?part=contentDetails&key=AIzaSyAWVhkyOnTdrJOJZizvsUuB8UvIgUH77Ew',
            favoriteUrl:'https://www.googleapis.com/youtube/v3/playlistItems?part=snippet&key=AIzaSyAWVhkyOnTdrJOJZizvsUuB8UvIgUH77Ew',
            deleteUrl:'https://www.googleapis.com/youtube/v3/playlistItems?part=snippet&key=AIzaSyAWVhkyOnTdrJOJZizvsUuB8UvIgUH77Ew',
            embedUrl:'https://www.youtube.com/embed/',
            userId:'',
            deletedId:'',
            favoriteId:'',
            accessToken:localStorage.getItem('token') || '',
            favoriteList:{},
            maxResults:'50',
        }
    },
    methods:{
        getUserInfo(){
            const {getUserUrl} = this
            axios.get(getUserUrl).then(({data})=>{
                   this.userId = data.items[0].id
                   this.getAllList()
                })
        },
         getAllList(){
                const {allListsUrl, userId} = this
                axios.get(allListsUrl +`&id=${userId}`).then(({data})=>{
                    const favoriteId = data.items[0].contentDetails.relatedPlaylists.favorites
                    this.getFavoriteList(favoriteId)
                })
        },
        getFavoriteList(favoriteId){
                const { favoriteUrl, maxResults } = this
                this.favoriteId = favoriteId
                axios.get(favoriteUrl + `&playlistId=${favoriteId}&maxResults=${maxResults}`).then(({data})=>{
                    this.favoriteList = data.items
                })      
        },
        deleteFavorite(id){
                this.deletedId=id
                const data ={
                     "id": this.deletedId 
                }
                const {deleteUrl,authenticate,accessToken} = this
                if(accessToken){
                    axios.post(deleteUrl,data).then(()=>{
                                console.log('success')
                                alert('刪除成功')
                        }).catch(()=>{
                            authenticate()
                        })
                }else{
                     authenticate()
                }
            },
        authenticate() {
                return gapi.auth2.getAuthInstance()
                    .signIn({scope: "https://www.googleapis.com/auth/youtube.force-ssl"})
                    .then((response)=> {
                        this.accessToken = response.wc.access_token
                        localStorage.token = response.wc.access_token
                        this.setting()        
                    }).catch((err)=>{
                            console.error("Error signing in", err);
                    })
            },
        setting(){
                    axios.interceptors.request.use(
                    (config) => {
                      config.headers.Authorization  = `Bearer ${localStorage.getItem('token')}` || this.accessToken
                      this.loadClient()
                        return config
                    },
                        err => Promise.reject(err),
                    )
            },
        loadClient() {
                gapi.client.setApiKey("AIzaSyAWVhkyOnTdrJOJZizvsUuB8UvIgUH77Ew");
                return gapi.client.load("https://www.googleapis.com/discovery/v1/apis/youtube/v3/rest")
                    .then(()=> { 
                    this.execute()
                    }).catch(()=>{
                        console.error("Error loading GAPI client for API")
                    })
            },
        execute() {
                     const { deletedId } = this
                    return gapi.client.youtube.playlistItems.delete({
                     "id": deletedId
                    })
                    .then((response) =>{
                        console.log("Response", response);
                        alert('刪除成功')
                    }).catch((err)=>{
                        console.error("Execute error", err);
                    })
            }
    },
    created(){
         axios.defaults.withCredentials = false
          gapi.load("client:auth2", function() {
            gapi.auth2.init({client_id: '878648823639-smhdtm84lnasgdg2vcf882tacri1fn5i.apps.googleusercontent.com',scope: 'https://www.googleapis.com/auth/youtube',});
        });
        // this.setting()
        this.getUserInfo()
    }
}
</script>